import getTime from "../../../getTime.js";
export default {
	data() {
		return {
			TabCur: 0,
			tabRow: ['已完成', '未完成', '逾期'],
			startTime: '',
			endTime: '',
			now: '',
			finishList: [],
			unfinishList: [],
			overDueList: [],
			nodata: 'http://data.huwei-soft.cn/schooltest/static/nodata.png',
			search2: 'http://data.huwei-soft.cn/schooltest/static/search2.png'
		}
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		},
		changetime(e) {
			if (e.target.dataset.time == 'start') {
				this.startTime = e.target.value;
			} else {
				this.endTime = e.target.value;
			}
		},
		getfinishList() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Troubleshooting_Finish',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					start_time: self.startTime,
					end_time: self.endTime,
					state: 2
				}
			}).then(data => {
				var [error, res] = data;
				console.log(res.data)
				if (res.data == '暂无数据') {
					self.finishList = []
				} else {
					self.finishList = JSON.parse(res.data)
				}
			})
		},
		
		getunfinishList() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Troubleshooting_Finish',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					start_time: self.startTime,
					end_time: self.endTime,
					state: 1
				}
			}).then(data => {
				var [error, res] = data;
				console.log(res.data)
				if (res.data == '暂无数据') {
					self.unfinishList = []
				} else {
					self.unfinishList = JSON.parse(res.data)
				}
			})
		},
		
		getOverDueList() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Troubleshooting_Finish',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					start_time: self.startTime,
					end_time: self.endTime,
					state: 3
				}
			}).then(data => {
				var [error, res] = data;
				console.log(res.data)
				if (res.data == '暂无数据') {
					self.overDueList = []
				} else {
					self.overDueList = JSON.parse(res.data)
				}
			})
		},
		
		SearchMore() {
			uni.showLoading({
				title:"加载中",
				mask: true
			})
			this.getfinishList();
			this.getunfinishList();
			this.getOverDueList();
			this.$nextTick(() => {
				uni.hideLoading()
			})
		}
	},
	onLoad(option) {
		console.log(option)
	},
	mounted() {
		this.now = getTime.now();
		this.SearchMore();
	}
}
